.TicketField{padding:3px;height:15px;width:60px;font-size:18px}.TicketField__title{text-align:center;background-color:transparent}.TicketField--crossed-out{background-image:-webkit-linear-gradient(top left, transparent calc(50% - 1px), #555, transparent calc(50% + 1px)),-webkit-linear-gradient(bottom left, transparent calc(50% - 1px), #555, transparent calc(50% + 1px));background-image:-o-linear-gradient(top left, transparent calc(50% - 1px), #555, transparent calc(50% + 1px)),-o-linear-gradient(bottom left, transparent calc(50% - 1px), #555, transparent calc(50% + 1px));background-image:linear-gradient(to bottom right, transparent calc(50% - 1px), #555, transparent calc(50% + 1px)),linear-gradient(to top right, transparent calc(50% - 1px), #555, transparent calc(50% + 1px))}.TicketField--clickable{cursor:pointer}.TicketField--clickable:hover{background-color:rgba(0,0,0,0.05)}



